import React, { useState, useEffect } from "react";
import AddNewPost from "../components/AddNewPost";
import PostPreview from "../components/PostPreview";
import Alert from "../components/uikit/Alert";
import Sort from "../components/Sort";
import styles from "./Blog.module.scss";
import firebase from "../firebase";

const Blog = (props) => {
  const [posts, setPosts] = useState([]);
  const [sortNewFirst, setSort] = useState(true);

  useEffect(() => {
    let cleanupFunction = false;

    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db.collection("posts");
      /*
       ** –ú–æ–∂–Ω–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–æ–≤–∞—Ç—å .get() –∏ onSnapShot().
       ** –ü–µ—Ä–≤—ã–π –≤–∞—Ä–∏–∞–Ω—Ç –ø–æ–∑–≤–æ–ª—è–µ—Ç –ø–æ–ª—É—á–∏—Ç—å —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –æ–¥–∏–Ω —Ä–∞–∑.
       ** –í—Ç–æ—Ä–æ–π –≤–∞—Ä–∏–∞–Ω—Ç –ø–æ–∑–≤–æ–ª—è–µ—Ç "—Å–ª—É—à–∞—Ç—å" –¥–æ–∫—É–º–µ–Ω—Ç –∏ –æ–±–Ω–æ–≤–ª—è—Ç—å —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –¥–æ–∫—É–º–µ–Ω—Ç–∞.
       */
      data.onSnapshot((snapshot) => {
        const newPosts = snapshot.docs.map((doc) => ({
          id: doc.id,
          date: doc.date,
          ...doc.data(),
        }));
        if (sortNewFirst) {
          // —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ (—Å–Ω–∞—á–∞–ª–∞ –Ω–æ–≤—ã–µ —Å–æ–æ–±—â–µ–Ω–∏—è)
          newPosts.sort((a, b) => {
            return new Date(b.date.seconds) - new Date(a.date.seconds);
          });
        } else {
          // —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ (—Å–Ω–∞—á–∞–ª–∞ —Å—Ç–∞—Ä—ã–µ —Å–æ–æ–±—â–µ–Ω–∏—è)
          newPosts.sort((a, b) => {
            return new Date(a.date.seconds) - new Date(b.date.seconds);
          });
        }
        /*
         ** –Ω–µ–ø–æ—Å—Ä–µ–¥—Å—Ç–≤–µ–Ω–Ω–æ–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ —Å–æ—Å—Ç–æ—è–Ω–∏—è –ø—Ä–∏ —É—Å–ª–æ–≤–∏–∏, —á—Ç–æ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –Ω–µ —Ä–∞–∑–º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω
         ** https://habr.com/ru/post/493496/ (–ø–æ–¥—Ä–æ–±–Ω–æ–µ –æ–ø–∏—Å–∞–Ω–∏–µ –æ—à–∏–±–∫–∏, —Å–≤—è–∑–∞–Ω–Ω–æ–π —Å —É—Ç–µ—á–∫–æ–π –ø–∞–º—è—Ç–∏)
         */
        if (!cleanupFunction) setPosts(newPosts);
      });
    };
    fetchData();

    // —Ñ—É–Ω–∫—Ü–∏—è –æ—á–∏—Å—Ç–∫–∏ useEffect
    return () => (cleanupFunction = true);
  }, [sortNewFirst]); // <<-- sortNew –≤ –º–∞—Å—Å–∏–≤–µ —è–≤–ª—è–µ—Ç—Å—è —Ç—Ä–∏–≥–≥–µ—Ä–æ–º, –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –∫–æ—Ç–æ—Ä–æ–≥–æ –ø—Ä–æ–∏—Å—Ö–æ–¥–∏—Ç –ø–µ—Ä–µ—Ä–∏—Å–æ–≤–∫–∞, –∞ [] —ç–º—É–ª–∏—Ä—É–µ—Ç —Ö—É–∫ ComponentDidMount (HTML —à–∞–±–ª–æ–Ω –≥–æ—Ç–æ–≤ –¥–ª—è –¥–∞–ª—å–Ω–µ–π—à–µ–π —Ä–∞–±–æ—Ç—ã)

  const sort = () => {
    setSort(!sortNewFirst);
  };

  return (
    <div className={styles.blog}>
      <Alert status="primary">
        –î–∞–Ω–Ω—ã–π –±–ª–æ–≥ —è–≤–ª—è–µ—Ç—Å—è —É—á–µ–±–Ω—ã–º –ø—Ä–æ–µ–∫—Ç–æ–º –∏ –Ω–µ –ø—Ä–µ—Ç–µ–Ω–¥—É–µ—Ç –Ω–∞ —Å—Ç–∞—Ç—É—Å
        –ø–æ–ª–Ω–æ—Ü–µ–Ω–Ω–æ–≥–æ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è. –ö–æ–¥–æ–≤–∞—è –±–∞–∑–∞ —Ä–∞—Å–ø–æ–ª–æ–∂–µ–Ω–∞ –Ω–∞ GitHub –ø–æ{" "}
        <a
          href="https://github.com/norzserge/blog"
          target="_blank"
          rel="noopener noreferrer"
        >
          —Å—Å—ã–ª–∫–µ
        </a>
      </Alert>
      <AddNewPost />
      <Sort isSort={sortNewFirst} onSort={sort} />
      <ul className={styles["blog-list"]}>
        {posts.map((post) => (
          /*
           ** –í –∫–∞—á–µ—Å—Ç–≤–µ key –ª—É—á—à–µ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å ID –∑–∞–ø–∏—Å–∏ (props.id), –∞ –Ω–µ index.
           ** –ò–Ω–∞—á–µ –≤–æ–∑–Ω–∏–∫–∞–µ—Ç –±–∞–≥ —É–¥–∞–ª–µ–Ω–∏—è —Å–ª–µ–¥—É—é—â–µ–π –∑–∞–ø–∏—Å–∏ –∑–∞ —É–¥–∞–ª–µ–Ω–Ω–æ–π.
           */
          <li key={post.id} className={styles["blog-item"]}>
            <PostPreview
              name={post.name}
              date={post.date.seconds}
              header={post.header}
              text={post.text}
              id={post.id}
              img={post.selectedOption}
            />
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Blog;
